/* ==========================================================================
   UTILITES
   ========================================================================== */

/* Visibility
   ========================================================================== */

[v-cloak] { display: none; }

/* Color
   ========================================================================== */

.light { color: $color_gray_light; }
.dark { color: $color_gray_dark; }

.bg-faint { background: $bg; }

.text-shadow {
    text-shadow: 0 1px 2px rgba(0,0,0,.25);
}

/* Misc
  ========================================================================== */

.loading {
	text-align: center;
	padding: 20px 0;

	.icon {
		display: block;
		margin-bottom: 5px;
	}

	&.loading-basic {
		text-align: left;
		padding: 0;

		.icon {
			display: inline-block;
			margin: -2px 5px 0 0;
		}
	}
}

.flexy {
    display: flex;
    align-items: center;

    &.baseline { align-items: baseline;}
    &.apart { justify-content: space-between; }
    &.column { flex-direction: column;}
    &.wrap { flex-wrap: wrap; }

    .fill { flex: 1; }
}

@media (min-width: 768px) {
    .lg_flex-no-wrap { flex-wrap: nowrap; }
    .lg_w-auto { width: auto; }
    .lg_mb-0 { margin-bottom: 0 !important; }
}

.loading-blocker {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 10; // arbitrary to make it appear over the fields
	background: rgba(white, .7);
}

// Saving overlay
.saving {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 200;
	background: rgba(white, .9);
	display: flex;
	align-items: center;
	border-radius: 3px;

	.inner {
		text-align: center;
		padding: 40px;
		margin: 0 auto;
	}

	i {
		position: relative;
		top: -1px;
		margin-right: 10px;
	}
}

.no-select {
	-webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.you-can-select-this {
    -webkit-touch-callout: text !important;
    -webkit-user-select: text !important;
    -khtml-user-select: text !important;
    -moz-user-select: text !important;
    -ms-user-select: text !important;
    user-select: text !important;
}


.help-block { width: 100%; }

.z-depth-0 { box-shadow: none !important; }
.z-depth-1 { box-shadow: 0 0 0 0.5px rgba(49,49,93,.03), 0 2px 5px 0 rgba(49,49,93,.1), 0 1px 2px 0 rgba(0,0,0,.08);}
.z-depth-2 { box-shadow: 0 7px 14px 0 rgba(50,50,93,.1), 0 3px 6px 0 rgba(0,0,0,.07); }



